"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { useToast } from "@/hooks/use-toast"
import { X, Loader2 } from "lucide-react"
import { detachMangoolsDomain } from "@/lib/actions/domains"
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog"

interface DetachDomainButtonProps {
  domainId: string
  datasourceId: string
}

export function DetachDomainButton({ domainId, datasourceId }: DetachDomainButtonProps) {
  const [loading, setLoading] = useState(false)
  const { toast } = useToast()

  async function handleDetach() {
    setLoading(true)
    try {
      const result = await detachMangoolsDomain(domainId)

      if (result.success) {
        toast({
          title: "Domain detached",
          description: "The domain has been removed from this data source.",
        })
      } else {
        toast({
          title: "Error",
          description: result.error,
          variant: "destructive",
        })
      }
    } catch (error) {
      toast({
        title: "Error",
        description: "An unexpected error occurred",
        variant: "destructive",
      })
    } finally {
      setLoading(false)
    }
  }

  return (
    <AlertDialog>
      <AlertDialogTrigger asChild>
        <Button size="sm" variant="ghost" disabled={loading}>
          {loading ? <Loader2 className="h-4 w-4 animate-spin" /> : <X className="h-4 w-4" />}
        </Button>
      </AlertDialogTrigger>
      <AlertDialogContent>
        <AlertDialogHeader>
          <AlertDialogTitle>Detach domain?</AlertDialogTitle>
          <AlertDialogDescription>
            This will remove the domain from this data source. Historical data will be preserved but the domain will no
            longer be tracked.
          </AlertDialogDescription>
        </AlertDialogHeader>
        <AlertDialogFooter>
          <AlertDialogCancel>Cancel</AlertDialogCancel>
          <AlertDialogAction onClick={handleDetach}>Detach</AlertDialogAction>
        </AlertDialogFooter>
      </AlertDialogContent>
    </AlertDialog>
  )
}
