"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { useToast } from "@/hooks/use-toast"
import { Plus, Loader2 } from "lucide-react"
import { attachMangoolsDomain } from "@/lib/actions/domains"
import type { MangoolsApiDomain } from "@/lib/supabase/types"

interface AttachDomainButtonProps {
  datasourceId: string
  domain: MangoolsApiDomain
}

export function AttachDomainButton({ datasourceId, domain }: AttachDomainButtonProps) {
  const [loading, setLoading] = useState(false)
  const { toast } = useToast()

  async function handleAttach() {
    setLoading(true)
    try {
      const result = await attachMangoolsDomain(datasourceId, domain)

      if (result.success) {
        toast({
          title: "Domain attached",
          description: `${domain.domain} has been added to this data source.`,
        })
      } else {
        toast({
          title: "Error",
          description: result.error,
          variant: "destructive",
        })
      }
    } catch (error) {
      toast({
        title: "Error",
        description: "An unexpected error occurred",
        variant: "destructive",
      })
    } finally {
      setLoading(false)
    }
  }

  return (
    <Button size="sm" onClick={handleAttach} disabled={loading}>
      {loading ? <Loader2 className="h-4 w-4 animate-spin" /> : <Plus className="h-4 w-4" />}
      {loading ? "Adding..." : "Add"}
    </Button>
  )
}
