"use client"

import { useState, useEffect } from "react"
import type { MangoolsDomain, MangoolsApiDomain } from "@/lib/supabase/types"
import { AttachDomainButton } from "./attach-domain-button"
import { AlertCircle, Loader2 } from "lucide-react"

interface AvailableDomainsSectionProps {
  datasourceId: string
  attachedDomains: MangoolsDomain[]
}

export function AvailableDomainsSection({ datasourceId, attachedDomains }: AvailableDomainsSectionProps) {
  const [domains, setDomains] = useState<MangoolsApiDomain[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    loadDomains()
  }, [])

  async function loadDomains() {
    try {
      setLoading(true)
      setError(null)
      // Call API route to fetch domains
      const response = await fetch("/api/mangools/domains")
      if (!response.ok) {
        throw new Error("Failed to fetch domains")
      }
      const data = await response.json()
      setDomains(data)
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to fetch domains")
    } finally {
      setLoading(false)
    }
  }

  const attachedDomainIds = new Set(attachedDomains.map((d) => d.mangools_id))
  const availableDomains = domains.filter((d) => !attachedDomainIds.has(d._id))

  return (
    <div>
      <h3 className="font-semibold text-foreground mb-3">Available Domains</h3>
      {error && (
        <div className="flex gap-2 text-sm text-destructive mb-3">
          <AlertCircle className="h-4 w-4 flex-shrink-0" />
          <span>{error}</span>
        </div>
      )}
      {loading ? (
        <div className="flex items-center justify-center py-6">
          <Loader2 className="h-4 w-4 animate-spin text-muted-foreground" />
          <span className="ml-2 text-sm text-muted-foreground">Loading domains...</span>
        </div>
      ) : availableDomains.length === 0 ? (
        <p className="text-sm text-muted-foreground py-4">
          {domains.length === 0 ? "No domains available in Mangools account" : "All domains are already attached"}
        </p>
      ) : (
        <div className="grid gap-2">
          {availableDomains.map((domain) => (
            <div key={domain._id} className="flex items-center justify-between p-3 border border-border rounded-md">
              <div className="flex-1">
                <p className="font-medium text-sm text-foreground">{domain.domain}</p>
                <p className="text-xs text-muted-foreground">
                  Location: {domain.location?.label ?? "N/A"} | Keywords: {domain.count ?? 0}
                </p>
              </div>
              <AttachDomainButton datasourceId={datasourceId} domain={domain} />
            </div>
          ))}
        </div>
      )}
    </div>
  )
}
