"use server"

import { createClient } from "@/lib/supabase/server"
import { revalidatePath } from "next/cache"
import { parseMangoolsDomainForDb } from "@/lib/mangools/api"
import type { MangoolsApiDomain } from "@/lib/supabase/types"

/**
 * Attach a Mangools domain to a datasource
 */
export async function attachMangoolsDomain(datasourceId: string, domain: MangoolsApiDomain) {
  try {
    const supabase = await createClient()

    // Parse domain data for storage
    const domainData = parseMangoolsDomainForDb(domain)

    // Insert or update the domain
    const { data: insertedDomain, error: domainError } = await supabase
      .from("mangools_domains")
      .upsert(
        {
          datasource_id: datasourceId,
          ...domainData,
          is_active: true,
        },
        {
          onConflict: "datasource_id,mangools_id",
        }
      )
      .select()
      .single()

    if (domainError) throw new Error(domainError.message)

    revalidatePath("/dashboard")
    return { success: true, data: insertedDomain }
  } catch (error) {
    const message = error instanceof Error ? error.message : "Failed to attach domain"
    console.error("Attach domain error:", error)
    return { success: false, error: message }
  }
}

/**
 * Detach a Mangools domain from a datasource
 * Soft delete by setting is_active to false
 */
export async function detachMangoolsDomain(domainId: string) {
  try {
    const supabase = await createClient()

    const { error } = await supabase.from("mangools_domains").update({ is_active: false }).eq("id", domainId)

    if (error) throw new Error(error.message)

    revalidatePath("/dashboard")
    return { success: true }
  } catch (error) {
    const message = error instanceof Error ? error.message : "Failed to detach domain"
    console.error("Detach domain error:", error)
    return { success: false, error: message }
  }
}
